@import "~normalize.css/normalize.css";
@import "~swiper/css/swiper.css";
:root {
  --default-bg: #5f458c;
  --indigo-bg: #a07de1;
  --blue-bg: #3e72ea;
  --cyan-bg: #90b8e0;
  --default-hover: #a67ee5;
  --indigo-hover: #5f458a;
  --blue-hover: #9fcefd;
  --cyan-hover: #3e72ea;
}

.body {
  --theme-color-bg: var(--default-bg);
  --theme-color-hover: var(--default-hover);
}

.body.theme-indigo {
  --theme-color-bg: var(--indigo-bg);
  --theme-color-hover: var(--indigo-hover);
}

.body.theme-blue {
  --theme-color-bg: var(--blue-bg);
  --theme-color-hover: var(--blue-hover);
}

.body.theme-cyan {
  --theme-color-bg: var(--cyan-bg);
  --theme-color-hover: var(--cyan-hover);
}

@font-face {
  font-family: "Proxima Nova";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/ProximaNova-Semibold.woff2") format("woff2"), url("../fonts/ProximaNova-Semibold.woff") format("woff");
}
@font-face {
  font-family: "Radical";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Radical.woff2") format("woff2"), url("../fonts/Radical.woff") format("woff");
}
:root {
  font-size: 0.69445vw;
}
@media (max-width: 1024px) and (orientation: portrait) {
  :root {
    font-size: 3.125vw;
  }
}
@media (max-width: 1024px) and (orientation: portrait) and (min-width: 768px) {
  :root {
    font-size: 2.5vw;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  :root {
    font-size: 1vw;
  }
}
@media (max-width: 1024px) and (orientation: landscape) and (max-width: 768px) {
  :root {
    font-size: 2vw;
  }
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 1.8rem;
  line-height: 1.1;
  font-weight: 600;
  color: #ffffff;
  background-color: #5f458c;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (min-width: 2000px) {
  body {
    font-size: 18px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  body {
    min-height: calc(var(--vh, 1vh) * 100);
    font-size: 1.4rem;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  body.menu-opened {
    overflow: hidden;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  body.menu-opened {
    overflow: hidden;
  }
}

a {
  text-decoration: none;
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: top;
}

.svg-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: -100;
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(2rem);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes fadeInBottom {
  from {
    opacity: 0;
    transform: translateY(2rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes scalingAppearance {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
@keyframes slidingBottom {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes slideRotateLeft {
  from {
    transform: translateX(-6.28rem) rotate(-180deg);
    opacity: 0;
  }
  to {
    transform: translateX(0) rotate(0);
    opacity: 1;
  }
}
@keyframes slideRotateRight {
  from {
    transform: translateX(6.28rem) rotate(180deg);
    opacity: 0;
  }
  to {
    transform: translateX(0) rotate(0);
    opacity: 1;
  }
}
.slide-text-bottom-word {
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  padding-top: 1.8rem;
  margin-top: -1.8rem;
}
.slide-text-bottom-word .slide-text-bottom-letter {
  display: inline-block;
  transform: translate3d(0, 130%, 0);
}

.animation-screen {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-image: url("../img/scene.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .animation-screen {
    background-image: url("../img/scene@2x.png");
  }
}
@media (orientation: portrait) {
  .animation-screen {
    background-image: url("../img/scene-mob.png");
  }
}
@media (orientation: portrait) and (min-resolution: 144dpi), (orientation: portrait) and (min-resolution: 1.5dppx) {
  .animation-screen {
    background-image: url("../img/scene-mob@2x.png");
  }
}

.btn {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem 2.9rem;
  text-transform: uppercase;
  text-align: center;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 3rem;
  color: #5f458c;
  letter-spacing: -0.04em;
  border-radius: 3.5rem;
  overflow: hidden;
}
.btn:focus {
  outline: none;
}
@media (min-width: 2000px) {
  .btn {
    padding: 15px 29px;
    font-size: 24px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .btn {
    padding: 1rem;
    font-size: 1.8rem;
    line-height: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .btn {
    padding: 1rem;
    font-size: 1.8rem;
    line-height: 2rem;
  }
}
.btn::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  border: 2px solid #ffffff;
  border-radius: 3.5rem;
  z-index: -1;
}
.btn:hover {
  color: #a67ee5;
}
.btn:focus {
  color: #a67ee5;
  outline: none;
}
.btn:focus::before {
  width: 103%;
  height: 108.5%;
  border-color: #a67ee5;
}
.btn:active, .btn:active:focus {
  color: #ffffff;
}
.btn:active::before, .btn:active:focus::before {
  width: 100%;
  height: 100%;
  background-color: #a67ee5;
  border-color: #a67ee5;
}

.chat {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 5.7rem;
  width: 100%;
}
@media (min-width: 2000px) {
  .chat {
    padding: 0 57px;
  }
}
@media (orientation: portrait) {
  .chat {
    padding: 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat {
    padding: 0;
  }
}

.chat__body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 5.9rem 0;
  width: 100%;
  max-height: calc(100vh - 33.9rem);
  max-width: 79.8rem;
  overflow-y: auto;
}
@media (min-width: 2000px) {
  .chat__body {
    padding: 0 59px 0;
    max-height: calc(100vh - 339px);
    max-width: 798px;
  }
}
@media (orientation: portrait) {
  .chat__body {
    padding: 0 0 0;
    max-height: calc(var(--vh, 1vh) * 100 - 22.4rem);
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .chat__body {
    max-height: calc(var(--vh, 1vh) * 100 - 25rem);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__body {
    padding: 0;
    max-height: calc(var(--vh, 1vh) * 100 - 16rem);
  }
}

.chat__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-top: auto;
}

.chat__message {
  flex: none;
  display: flex;
  margin-bottom: 2rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
}
@media (min-width: 2000px) {
  .chat__message {
    margin-bottom: 20px;
    font-size: 24px;
  }
}
@media (orientation: portrait) {
  .chat__message {
    font-size: 1.6rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__message {
    font-size: 1.6rem;
  }
}
.chat__message p {
  margin: 0;
  padding: 1.6rem 5.7rem;
  min-width: 17rem;
  border-radius: 4rem;
}
@media (min-width: 2000px) {
  .chat__message p {
    padding: 16px 57px;
    min-width: 170px;
    border-radius: 40px;
  }
}
@media (orientation: portrait) {
  .chat__message p {
    padding: 1rem 2.5rem;
    min-width: 10rem;
    max-width: calc(100% - 5rem);
    border-radius: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__message p {
    padding: 1rem 2.5rem;
    min-width: 10rem;
    max-width: calc(100% - 5rem);
    border-radius: 2rem;
  }
}
.chat__message--outcoming {
  justify-content: flex-start;
}
.chat__message--outcoming p {
  width: 100%;
  color: #5f458c;
  background-color: #ffffff;
  word-break: break-word;
}
.chat__message--incoming {
  justify-content: flex-end;
  text-align: center;
}
.chat__message--incoming p {
  color: #ffffff;
  background-color: #a67ee5;
  word-break: break-word;
}

.chat__placeholder {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 17rem;
  height: 6rem;
  background-color: #a67ee5;
  border-radius: 3rem;
}
@media (min-width: 2000px) {
  .chat__placeholder {
    width: 170px;
    height: 60px;
    border-radius: 30px;
  }
}
@media (orientation: portrait) {
  .chat__placeholder {
    width: 10rem;
    height: 4rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__placeholder {
    width: 10rem;
    height: 4rem;
  }
}
.chat__placeholder span {
  margin: 0 0.65rem;
  width: 1rem;
  height: 1rem;
  background-color: #ffffff;
  border-radius: 50%;
}
@media (min-width: 2000px) {
  .chat__placeholder span {
    margin: 0 6.5px;
  }
}
@media (orientation: portrait) {
  .chat__placeholder span {
    margin: 0 0.5rem;
    width: 0.6rem;
    height: 0.6rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__placeholder span {
    margin: 0 0.5rem;
    width: 0.6rem;
    height: 0.6rem;
  }
}
.chat__placeholder--hidden {
  opacity: 0;
}

.chat__footer {
  margin-top: auto;
}

.disclaimer {
  color: #5f458c;
  background-color: #ffffff;
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer {
    font-weight: 400;
    color: #ffffff;
    background-color: transparent;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer {
    font-weight: 400;
    color: #ffffff;
    background-color: transparent;
  }
}
.disclaimer ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-size: 1.2rem;
  line-height: 1.2;
}
@media (min-width: 2000px) {
  .disclaimer ul {
    font-size: 12px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer ul {
    font-size: 1rem;
    opacity: 0.5;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer ul {
    font-size: 1rem;
    opacity: 0.5;
  }
}
.disclaimer li {
  position: relative;
  padding: 0 0.6rem;
}
@media (min-width: 2000px) {
  .disclaimer li {
    padding: 0 6px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer li {
    padding: 0 0.4rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer li {
    padding: 0 0.4rem;
  }
}
.disclaimer li::before {
  content: "/";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-50%, -50%);
}
.disclaimer li:first-child::before {
  display: none;
}
.disclaimer a {
  display: inline-block;
  vertical-align: top;
  padding: 0.9rem 0 0.7rem;
  text-decoration: underline;
  color: inherit;
}
.disclaimer a:hover {
  opacity: 0.8;
}
.disclaimer a:focus {
  outline: none;
  opacity: 0.7;
}
@media (min-width: 2000px) {
  .disclaimer a {
    padding: 9px 0 7px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer a {
    padding: 0.9rem 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer a {
    padding: 0.9rem 0;
  }
}

.form {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.form__label {
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 1.2rem;
  width: 100%;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.6rem;
  line-height: 1.2;
  opacity: 0;
  pointer-events: none;
}
@media (min-width: 2000px) {
  .form__label {
    margin-bottom: 12px;
    font-size: 16px;
  }
}
@media (orientation: portrait) {
  .form__label {
    opacity: 1;
    pointer-events: auto;
  }
}

.form__field-wrapper {
  overflow: hidden;
  width: calc(100% - 21rem);
  margin-right: 3rem;
  border-radius: 3rem;
}
@media (min-width: 2000px) {
  .form__field-wrapper {
    width: calc(100% - 210px);
    margin-right: 30px;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form__field-wrapper {
    width: calc(100% - 9rem);
  }
}
@media (max-width: 768px) and (orientation: landscape), (orientation: portrait) {
  .form__field-wrapper {
    width: calc(100% - 5rem);
    margin-right: 1rem;
  }
}

.form__field {
  flex: none;
  padding: 0 5.6rem;
  height: 6rem;
  width: 100%;
  text-transform: uppercase;
  font: inherit;
  font-size: 2.4rem;
  line-height: 1.2;
  color: #5f458c;
  border: 0;
  border-radius: 3rem;
  outline: none;
  transform: translateX(92%);
}
.form__field:focus {
  border: 1px solid #a67ee5;
}
@media (min-width: 2000px) {
  .form__field {
    padding: 0 56px;
    height: 60px;
    font-size: 24px;
    border-radius: 30px;
  }
}
@media (orientation: portrait) {
  .form__field {
    padding: 0 2.5rem;
    height: 4rem;
    font-size: 1.6rem;
    transform: translateX(82%);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .form__field {
    padding: 0 2.5rem;
    height: 4rem;
    font-size: 1.6rem;
  }
}

.screen--game.active .form__field {
  animation: formFieldAppear 0.3s ease forwards;
}
.screen--game.active .form__field::placeholder {
  opacity: 1;
}

@keyframes formFieldAppear {
  from {
    opacity: 1;
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@media (orientation: portrait) {
  .form__button {
    position: relative;
    flex: none;
    padding: 0;
    width: 4rem;
    height: 4rem;
    font-size: 0;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form__button {
    position: relative;
    flex: none;
    padding: 0;
    width: 6rem;
    height: 6rem;
    font-size: 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .form__button {
    width: 4rem;
    height: 4rem;
  }
}
.form__button svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  width: 1.6rem;
  height: 1.8rem;
}
@media (min-width: 2000px) {
  .form__button svg {
    width: 16px;
    height: 18px;
  }
}
@media (orientation: portrait) {
  .form__button svg {
    display: block;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form__button svg {
    display: block;
  }
}

.form--result .form__field-wrapper {
  width: calc(100% - 31rem);
}
@media (min-width: 2000px) {
  .form--result .form__field-wrapper {
    width: calc(100% - 310px);
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form--result .form__field-wrapper {
    width: calc(100% - 9rem);
  }
}
@media (max-width: 768px) and (orientation: landscape), (orientation: portrait) {
  .form--result .form__field-wrapper {
    width: calc(100% - 5rem);
  }
}

.screen--result .form__field {
  opacity: 0;
}
.screen--result .form__field::placeholder {
  opacity: 0;
  transition: opacity 0.3s ease 0.8s;
}
.screen--result .form__button {
  opacity: 0;
  color: transparent;
}
.screen--result.screen--show .form__field {
  animation: formFieldAppear 0.3s ease 0.6s forwards;
}
.screen--result.screen--show .form__field::placeholder {
  opacity: 1;
}
.screen--result.screen--show .form__button {
  animation: formButtonFadeIn 0.3s 0.6s ease forwards;
}

@keyframes formButtonFadeIn {
  50% {
    opacity: 1;
    color: transparent;
  }
  to {
    opacity: 1;
    color: #5f458c;
  }
}
